.card__container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    gap: .75rem;
    width: 13.75rem;
    padding: .25rem .5rem;
    border: 1px solid transparent;
    border-radius: .5rem;
    text-decoration: none;
    color: var(--secondary-color);
    padding-bottom: 1rem;
}
.card__container:hover{
    border-color:#8886;
    background-color: var(--primary-color);
}
h3, p{
    margin: 0;
}
.card-image__content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 98%;
    cursor: pointer;
}

.product-img{
    max-width: 92%;
    width: 100%;
    mix-blend-mode: multiply;
}
.card__content{
    max-width: 98%;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
}

.description-title{
    font-size: .8rem;
    font-weight: 600;
    color: var(--text-color);
}

.app-free-shipping{
    width: 100%;
}

.app-btn-action{
    visibility: hidden;
    height: 3rem;
    width: 100%;
}
.card__container:hover > .app-btn-action{
    visibility: visible;
}

.card__price{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    line-height: 1.375rem;
    margin-bottom: 1rem;
    margin-top: .5rem;
    font-weight: 600;
}

.price-before{
    color: var(--text-color);
    text-decoration: line-through;
    font-size: 0.75rem;
}

.price-after{
    font-size: 1.25rem;
    color: var(--secondary-color);
    font-weight: 600;
    margin-bottom: .25rem;
}

.price-installments{
    color: var(--text-color);
}